@import url(https://fonts.googleapis.com/css?family=Big+Shoulders+Text&display=swap);
body {
  margin: 0;
  font-family: 'Big Shoulders Text', sans-serif;
  font-size: 100%;
}

h1, h2, h3, h4, h5, p, a, ul {
  font-family: 'Montserrat', sans-serif;
}

h1 {
  font-size: 1.7rem;
  font-weight: 900;
}

h1 {
    text-align: left;
    color: #333333;
    font-weight: 900;
    padding: 10px 0;
    font-size: 1.5rem;
}

h2 {
    text-align: left;
    color: #555555;
    font-weight: 900;
    padding: 10px 0;
    font-size: 1.3rem;
}

p {
    text-align: left;
    color: #111;
    font-weight: 100;
