    text-align: justify;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.route-wrapper {
  position: absolute;
}

.route-wrapper > div {
  position: absolute;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.wrapperHome {
  margin: 0;
  width: 100%;
  height: 100vh;
}

.wrapperHome .row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  padding: 0;
  margin: 0;
}

.wrapperHome .column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1 1;
  background: #000000;

}

.wrapperHome .column.contenido {
  flex: 12 1;
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

.wrapperHome .blue-column {
  height: 100vh;
  display: flex;
  align-items: center;
  background: #111;
  justify-content: center;
  box-shadow: 0 0 10px inset #000000;
}

.wrapperHome .blue-column i.material-icons {
  font-size: 2em;
  color: #ffffff;
  margin: 20px 0;
  font-size: 1.2em;
  transition: all 0.5s;
}

.wrapperHome .blue-column i.material-icons:hover {
  color: #ff0000;
}

.wrapperHome .green-column {
  height: 100vh;
  display: flex;
  align-items: center;
  text-align: left;
  padding: 20px;
  color: #333;
}

.wrapperHome .green-column a {
  color: #000000;
}

.btn {
  background-color: gold;
  color: #000000;
  text-align: center;
  padding: 10px 25px;
  border-radius: 0;
  font-weight: 900;
  text-transform: uppercase;
  margin: 15px 0;
  transition: all 1s;
}

.btn:hover {
  background-color: #000000;
  color: gold;
}

.navbar-light .navbar-nav .nav-link {
  text-align: left;
}
.menu {
    width: 100%;
    background: #2d3e50;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.menu .datos.personales h1 {
    font-size: 1.4rem;
    font-weight: 900;
    padding: 10px 0;
    padding-bottom: 0;
    text-align: center;
    color: #ffffff;
}

.menu .datos.personales h2 {
    font-size: 1rem;
    font-weight: 200;
    padding: 2px 0;
    color: #a3aeba;
    padding-bottom: 0;
    border-bottom: 1px solid #2d3e50;
    text-align: center;
}

.menu .datos.personales {
    color: #ffffff;
}

.menu .datos.personales img {
    border-radius: 50%;
    max-width: 80px;
    width: 80px;
    box-shadow: 0 0 10px #000000;
}

.menu .flex-column.nav {
    text-align: left;
    margin: 10px 0;
    width: 100%;
}

.navbar-light .navbar-nav a {
    font-size: 0.8rem;
    color: #ffffff;
    margin: 0;
    padding: 0;
    background: #29333e;
    padding: 5px;
    padding-left: 20px;
    margin-bottom: 2px;
    transition: all 1s;;
    text-align: left;
    width: 100%;
    display: block;
    margin-bottom: 3px;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    align-content: center;
}

.navbar-expand .navbar-nav {
    width: 100%;
}

.menu .flex-column.nav a:hover {
    background: #566e86;
}

.navbar-light .navbar-nav a:hover {
    background-color: gold;
    color: #222222;
    text-decoration: none;
}

.navbar-light .navbar-nav a.active {
    background-color: gold;
    color: #222222;
}

.menu p {
    padding: 0px;
    font-size: 0.7rem;
    color: #ffffff;
}

i.material-icons {
    position: relative;
    float: left;
    border: 1px solid #000;
    padding: 5px;
    margin-right: 5px;
    margin-top: -5px;
    border-radius: 50%;
}

.navbar-expand .navbar-collapse {
    width: 100%;
    margin-top: 20px;
}

nav.menucv {
    padding: 0;
    margin: 0;
    width: 100%;
}

.menucv span.material-icons {
    position: relative;
    float: right;
    background: #111111;
    padding: 2px;
}

.navbar-light .navbar-nav .nav-link.active {
    background: gold !important;
}

.navbar-light .navbar-nav a.active span.material-icons {
    color: gold;
}

p.text-left i.material-icons {
    font-size: 1em;
    margin-top: -2px;
}

.navbar__link--active {
  border-bottom: 3px solid #29b6f6;
  transition: border-bottom .5s ease-in-out;
}
.intro {
    background-color: #333;
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.intro h1 {
    font-weight: bold;
    text-transform: uppercase;
    font-size: 4em;
    text-align: center;
    color: #ffffff;
}

.intro h2 {
    font-weight: bold;
    text-transform: uppercase;
    font-size: 2rem;
    color: #587ca0;
    text-align: center;
}

.intro p {
    text-align: center;
    padding: 0 90px;
    font-weight: 100;
    font-size: 1rem;
    color: #ffffff;
}

@media only screen and (max-width: 1024px) {
    .intro {
        display: block;
    }
    .icod {
        display: none;
    }
    .intro h1 {
        font-size: 3rem !important;
        width: 100%;
    }
}

@media only screen and (max-width: 780px) {
    .intro {
        display: block;
    }
    .icod {
        display: none;
    }
    .intro h1 {
        font-size: 3rem !important;
    }
}
.ps h1 {
    text-align: left;
    color: #333333;
    font-weight: 900;
    padding: 10px 0;
    font-size: 1.5rem;
}

.ps h2 {
    text-align: left;
