}

.ps ul {
    margin: 0;
    padding: 0;
    list-style: none;
    text-align: left;
    font-size: 0.8rem;
    color: #333333;
    list-style: square;
    padding-left: 20px;
}

.ps .material-icons {
    margin: 0;
    padding: 0;
    position: relative;
    float: left;
    margin-top: -3px;
    margin-right: 10px;
    border: 1px solid #2C3E50;
    color: #2C3E50;
    padding: 3px;
    border-radius: 50%;
}

.ps .iconosDev i {
    color: #333333;
    margin: 0 3px;
}

.ps.tech {
    align-items: center;
    justify-content: center;
}

.ps.ts.ed h3 {
    text-align: left;
    color: #555;
}

